import React from "react";
import FoliaCard from "./FoliaCard";
import { MDBIcon, MDBRow, MDBCol } from "mdbreact";

import Slider from "react-animated-slider";
import "react-animated-slider/build/horizontal.css";

export default function FoliaCarousel(props) {
  const secaoTituloSt = { color: "#4A4A4A!important" };
  let sliderRef;

  function slidePrevious() {
    if (sliderRef) {
      sliderRef.previous();
    }
  }

  function slideNext() {
    if (sliderRef) {
      sliderRef.next();
    }
  }

  return (
    <section className="text-center my-3 w-100">
      <MDBRow
        className="h1-responsive font-weight-bold my-2"
        style={secaoTituloSt}
      >
        <MDBCol size="8">
          <h2 className="text-left ">{props.titulo}</h2>
        </MDBCol>
        <MDBCol size="4">
          <h2 className="text-right">
            <MDBIcon
              className="mr-4"
              icon="angle-left"
              onClick={slidePrevious}
            />
            <MDBIcon icon="angle-right" onClick={slideNext} />
          </h2>
        </MDBCol>
      </MDBRow>

      <Slider
        duration={1000}
        ref={ref => (sliderRef = ref)}
        previousButton={null}
        nextButton={null}
      >
        {props.dados
          .filter(function(produtos) {
            return produtos.type === "grouped";
          })
          .map(item => (
            <div key={item.id}>
              <FoliaCard dados={item} />
            </div>
          ))}
      </Slider>
    </section>
  );
}
