import React from "react";
import { MDBView, MDBMask, MDBBadge } from "mdbreact";
import { Link } from "react-router-dom";

export default function FoliaCard(props) {
  const produto = props.dados;
  const atributos = produto.attributes;
  const censura = atributos.find(obj => obj.name === "Censura");
  //const modalidade = atributos.find(obj => obj.name === "Modalidade");
  const imgurl = produto.images[0] ? produto.images[0].src : "";

  return (
    <Link to="/compre-aqui">
      <MDBView>
        <img src={imgurl} className="img-fluid w-100" alt={produto.name} />
        <MDBMask
          overlay="black-light"
          className="d-flex flex-column text-white p-2"
        >
          <div className="text-left">
            <MDBBadge color="dark" className="p-1" title="censura">
              {censura ? censura.options[0] : ""}
            </MDBBadge>
          </div>
          <div className="text-left h4-responsive">{produto.name}</div>
          <div className="text-left">
            A partir de
            <b> R$ {produto.price}</b>
          </div>
        </MDBMask>
      </MDBView>
    </Link>
  );
}
