import React, { useState } from "react";
import ItemsCarousel from "react-items-carousel";
import BotaoServico from "./BotaoServico";

export default () => {
  const [activeItemIndex, setActiveItemIndex] = useState(0);
  const chevronWidth = 0;

  return (
    <section id="barra-servicos" className="text-center mt-5 mb-2">
      <div style={{ padding: `0 ${chevronWidth}px` }}>
        <ItemsCarousel
          requestToChangeActive={setActiveItemIndex}
          activeItemIndex={activeItemIndex}
          numberOfCards={3}
          gutter={10}
          leftChevron={""}
          rightChevron={""}
          outsideChevron={false}
          chevronWidth={chevronWidth}
        >
          <BotaoServico caption="Central de Ajuda" icon="question-circle" />
          <BotaoServico caption="Passaporte da Folia" icon="passport" />
          <BotaoServico caption="Central de Atendimento" icon="headset" />
          <BotaoServico caption="Conheça os circuitos" icon="location-arrow" />
        </ItemsCarousel>
      </div>
    </section>
  );
};
